.noAccessBg {
  color: black;
  font-weight: bolder;
  /* background:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url("./Lenovo-banner.png"); */
  background-size: 100% 100%;
  height: 730px;
}

.pageName {
  color: white;
  margin-left: 7px;
  font-size: 21px;
}

.kpi-row {
  height: 30%;
  display: flex;
  justify-content: space-around;
  /* background-color: deeppink; */
  /* padding-top: 10px; */
  padding-bottom: 10px;
}

.bottom-row {
  height: 70%;
}

.hidden {
  display: none;
}

.kpi-top-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.kpi-bottom-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.line-container {
  display: flex;
  justify-content: center;
}

.row {
  flex-wrap: nowrap;
  /* flex-wrap: wrap; */
}

*:focus {
  outline: none !important;
  box-shadow: none;
}

.slick-slider {
  height: 100%;
}

.slick-list {
  height: 100%;
}

.slick-track {
  height: 100%;
}

.slick-slide {
  height: 100%;
}

.slick-slide > div {
  height: 100%;
}

.slick-slide > div > div {
  height: 100%;
  display: inherit !important;
}

.quarter {
  float: right;
  margin-right: 7px;
  font-size: 14px;
  font-weight: bolder;
}

.snapDate {
  font-style: italic;
  font-size: 14px;
  margin-left: 7px;
  margin-bottom: 5px;
  font-weight: bolder;
}

.quarterRange {
  font-style: italic;
  font-size: 14px;
  margin-right: 7px;
  float: right;
  font-weight: bolder;
}

.chart {
  /* background-color: #a25d5d91; */
  margin-top: 25px;
  /* margin-bottom: 25px; */
  padding-top: 10px;
  padding-bottom: 10px;
}

.page-subTitle {
  font-weight: 500;
  font-size: 24px;
  color: #64131e;
}

.chart_card {
  background-color: var(--card-bg-color);
  border-radius: 5px;
  border: 3px solid #f3f4f4;
  margin: 2px;
  padding: 5px;
}

@media (max-width: 768px) {
  .row {
    /* flex-wrap: nowrap; */
    flex-wrap: wrap;
  }
}

button {
  min-width: 25%;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  background-color: white;
  font-weight: bold;
}

.App {
  border: 2px solid #e6e2e4;
  border-radius: 16px;
}
